function generateFullName(): string {
  // Chn h ngu nhin
  const familyName =
    familyNames[Math.floor(Math.random() * familyNames.length)];

  // Chn tn m ngu nhin
  const middleName =
    middleNames[Math.floor(Math.random() * middleNames.length)];

  // Chn tn chnh ngu nhin
  const givenName = givenNames[Math.floor(Math.random() * givenNames.length)];

  // Kt hp h, tn m, v tn chnh
  return `${familyName} ${middleName} ${givenName}`;
}

@Table({ tableName: 'account' })
export class Account extends Model {
  @PrimaryKey
  @Column({
    type: DataType.UUID,
    defaultValue: DataType.UUIDV4,
  })
  override id!: string;

  @Column
  email!: string;

  @Column
  password!: string;

  @Column({
    type: DataType.STRING,
  })
  role!: Role;

  @Column({
    type: DataType.BOOLEAN,
    defaultValue: false,
  })
  isVerify!: boolean;

  @Column({
    defaultValue: CredentialTypeEnum.NONE,
    type: DataType.STRING,
  })
  credentialType!: CredentialTypeEnum;

  @HasOne(() => Profile)
  profile!: Profile;

  @Column({
    type: DataType.DATE,
    defaultValue: DataType.NOW,
  })
  override createdAt!: Date;

  @Column({
    type: DataType.DATE,
    defaultValue: DataType.NOW,
  })
  override updatedAt!: Date;

  @AfterCreate
  static async createProfile(instance: Account) {
    const defaultAccount = {
      ...DefaultProfileValue,
      nickName: generateRandomNickName(),
      accountId: instance.id,
      fullName: generateFullName(),
    };
    try {
      await Profile.create(defaultAccount);
    } catch (error) {
      console.error('Error creating profile:', error);
    }
  }
}
