import { ApiProperty } from '@nestjs/swagger';
import { IsNotEmpty } from 'class-validator';

export class UpdateSectDto {
  @IsNotEmpty()
  @ApiProperty({
    example: 'xxxx',
  })
  id!: string;

  @ApiProperty({
    example: 'Frontend',
  })
  name!: string;

  @ApiProperty({
    example: 'M mn phi hin ti.',
  })
  description!: string;

  @ApiProperty({
    example: 'M t hnh nh ca mn phi',
  })
  logo!: string;
}
