import { ApiProperty } from '@nestjs/swagger';
import { IsNotEmpty } from 'class-validator';

export class CreateMaterialArtDto {
  @IsNotEmpty()
  @ApiProperty({
    example: 'Angular Thn Cng',
  })
  name!: string;

  @IsNotEmpty()
  @ApiProperty({
    example: 'M t mn v hc',
  })
  description!: string;

  @IsNotEmpty()
  @ApiProperty({
    example: 'Thng tin Id ca mn phi to ra mn v hc ny.',
  })
  sectId!: string;
}
