import { ApiProperty } from '@nestjs/swagger';
import { IsNotEmpty } from 'class-validator';

export class CreateAchievementDto {
  @IsNotEmpty()
  @ApiProperty({
    example: 'S cp thut o',
  })
  name!: string;

  @ApiProperty({
    example: 'Hnh nh m t cp bc cnh gii hin ti',
  })
  @IsNotEmpty()
  logo!: string;
}
