import { ApiProperty } from '@nestjs/swagger';
import { IsNotEmpty } from 'class-validator';

export class CreateRealmDto {
  @IsNotEmpty()
  @ApiProperty({
    example: 'Luyn kh cnh',
  })
  name!: string;

  @ApiProperty({
    example: 'M t cnh gii hin ti',
  })
  @IsNotEmpty()
  description!: string;

  @ApiProperty({
    example: 'M t cp bc cnh gii hin ti',
  })
  @IsNotEmpty()
  level!: number;
}
